import React from 'react'
import Home from './Home.js'

export default function Front() {
    return (
        <div className="parent" style={{width:"100vw", height:"100vh", backgroundColor:"#EFEFEF",overflow:"hidden"}}>
            <div className="child" style={{backgroundColor: "white", width:"50vw", height:"80vh",transform:"translate(50%,10%)", overflowY:"auto"}}>
                <Home/>
            </div>    
        </div>
    )
}
