/** @jsx jsx */
import React from 'react';
import {Global, css, jsx } from '@emotion/core';
import {Link} from 'react-router-dom';
import Typical from 'react-typical';

import tictac from "../img/tictac.png";
import api from "../img/api.png";
import spotify from "../img/spotify.png"



 const Portfolio =() => {
const [array, setArray] = React.useState(["tictactoe", "api", "partypopper"]);
const [search, setsearch] = React.useState("");

    return (
<div  >
    <div  css={css`padding-top: 4vh;`}>
    <input type="text" placeholder="Search"  css={css`width: 30vw; height: 5vh; transform: translate(35vw);
    border: none; border-bottom : 1px solid black;:focus{
        outline: none;
        
    }; `}
    onChange={(e) => setsearch(e.target.value.toLowerCase())} />
    </div>
    
      
<div css={css`display: grid;
        grid-template: repeat(3, 1fr) / repeat(3, 1fr);`}>
          
  {array[0].includes(search) && <div css={css`width:auto; height: auto; margin-top:30px;`}>
        <Link to="/game">
            <img src={tictac} css={css`width: 30vw; height: 30vh`} />
        </Link>  
        <h3 css={css`text-align: center;`}>Tictactoe</h3>
    </div>}

    {array[1].includes(search) && <div css={css`width:auto; height: auto; margin-top:30px;`}>
        <Link to="/api">
            <img src={api} css={css` display: flex; justify-content: center; height: 30vh; margin-left:30%`} />
        </Link>  
        <h3 css={css`text-align: center;`}>Api</h3>
    </div>}
    {array[2].includes(search) &&  <div css={css`width:auto; height: auto; margin-top:30px;`}>
        <a href="https://github.com/tawsif1/music_controller">
            <img src={spotify} css={css` display: flex; justify-content: center; height: 30vh; margin-left:15%`} />
        </a>  
        <h3 css={css`text-align: center;`}>Party Popper</h3>
    </div>}
    

    
</div>


</div>
            
       
        
        
    );
}


export default Portfolio

