import React , { useEffect, useState } from 'react';
import "../css/Home.css";



export default function Home() {
    const url = "https://api.hatchways.io/assessment/students";
    
    const [search, setSearch] = useState("");
    const [info, setInfo] = useState();
    const [open, setOpen] = useState(false);
    
    const [select, setSelect] = useState([]);
    
    

    fetch(url)
    .then((response) => response.json())
    .then((data) => {
      setInfo(data.students);
      console.log(info)
    });

    const average = arr => {
      var sum = 0;
      for(var i =0; i<arr.length; i++){
         sum += parseInt(arr[i]);
      }
      
      return sum/arr.length

    }

    const toggleShow = id =>{
      const arr = select.slice();
      const i = select.indexOf(id);

      if(i >=0){
        arr.splice(i,1);
        setSelect(arr);
      }
      else{
        arr.push(id);
        setSelect(arr);
        console.log(select);

      }

    }

  return (
        !info ?
        ( <div className="load"> loading...  </div>)
      : (
<div className="container" >
  <input className="input" placeholder="Search" onChange={event => {setSearch(event.target.value)}} />
        
    
        
  {info.filter(result=>{
        if(result.firstName.toLowerCase().includes(search.toLowerCase()))
              {
                return result;
              }else if(search == "")
              {
                return result;
              }}).map(result =>    
  <div className="exterior" >
    <div className="cover">
       <img className="image" src={result.pic} alt="Avatar woman" />   
    </div>

    <div className="text" >
      <h1 > {result.firstName} {result.lastName}</h1>
   
      <text> email: {result.email}</text>
      <br/>
      <text> Company: {result.company}</text>
      <br/>
      <text> Skill: {result.skill}</text>
      <br/>
      <text> Average: {average(result.grades)}</text>
      <br/>
      
      
   </div >

   <div className="covertwo" >
      <button className="button" onClick={()=>toggleShow(result.id)}> 
        {select.includes(result.id) ? "-" : "+"}
      </button>
   </div>
   
    {select.includes(result.id) &&
    (<div id={result.id} className="coverthree" >
     {result.grades.map((ele,ind)=><span style={{display:"block"}}> test{ind+1}: {ele} </span>)}
    </div>)}
   

  </div>)} 
</div>))}
